import React from 'react';
import { Carousel,NavBar,Icon,WingBlank,SearchBar } from 'antd-mobile';

export default class Shop extends React.Component {
  constructor(props) {
    super(props);
      this.state = {
        data: ['1', '2'],
        imgHeight: 176,
      }

  }


  render() {
    return(
      <div>
      <NavBar
        className="method-nav"
        mode="light"
        onLeftClick={() => console.log('onLeftClick')}
        rightContent={[
          <Icon key="0" type="shop" style={{ marginRight: '16px' }} />
        ]}
      ><span>商城</span></NavBar>

      <div>
        <SearchBar style={{opacity:"0.9"}} placeholder="Search" maxLength={8} />
        <div>
        <WingBlank>
      <Carousel
        autoplay={true}
        infinite={true}
        beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
        afterChange={index => console.log('slide to', index)}
      >
        {this.state.data.map(val => (
          <a
            key={val}
            href="http://www.alipay.com"
            style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
          >
            <img
              width={{width:'100%'}}
              src={'./images/banner-'+val+'.png'}
              alt=""
              style={{ width: '100%', verticalAlign: 'top' }}
              onLoad={() => {
                // fire window resize event to change height
                window.dispatchEvent(new Event('resize'));
                this.setState({ imgHeight: 'auto' });
              }}
            />
          </a>
        ))}
      </Carousel>
    </WingBlank>
        </div>

        <div className="icon-sell-container">

            {
              ["桌","床","椅","几","柜","书架","沙发","家居","户外"].map((value)=> {
                return(
                  <div className="icon-sell-item">
                    <span>{value}</span>
                  </div>
                )
              })
            }



        </div>
        <div className="onsell">
          <img src='./images/cup.png'/>
          <span>Top Art Studio 欧式风格精细...</span>
        </div>
        <div className="onsell">
          <img src='./images/goods.png'/>
          <span>顺顺工艺欧式风格塑料外框黑...</span>
        </div>

      </div>
      </div>

    )
  }
}
