import React,{Component} from 'react';
import {NavBar,Icon,Input} from 'antd-mobile';
import { List,Button,InputItem} from 'antd-mobile';
export default class Login extends Component {
  render() {
    return(
      <div className="container">
      <NavBar
      mode="light"
      icon={<Icon type="left" />}
      onLeftClick={() => console.log('onLeftClick')

      }
      >登录</NavBar>
      <div>
          <List className="login-container">
            <InputItem
              clear
              placeholder="手机/邮箱"
              ref={el => this.autoFocusInst = el}
            ><img src="./images/登录1.png" /></InputItem>
            <InputItem
              clear
              placeholder="密码"
              ref={el => this.inputRef = el}
            ><img src="./images/密码.png" /></InputItem>
            <Button type="primary" className="login-button">登录</Button>
            <div className="register-container">
              <a>新用户注册</a>|<a>忘记密码</a>
            </div>
            <div className="other-login">
              <p>第三方登录</p>
            </div>
          </List>
          <div className="other-img-container">
            <img src="./images/qq.png"/>
            <img src="./images/微信.png"/>
            <img src="./images/微博.png"/>
          </div>
      </div>
      </div>

    )
  }
}
