import React,{ Component } from 'react';
import { WingBlank,Carousel,TabBar,NavBar,Icon,Badge,Tabs } from 'antd-mobile';
import '../css/Index.css';
import Shop from './Shop';

const tabs = [
  { title: <Badge>推荐</Badge> },
  { title: <Badge>冬季</Badge> },
  { title: <Badge>宜家</Badge> },
  { title: <Badge>小清新</Badge> },
  { title: <Badge>小户型</Badge> },
  { title: <Badge>个性色彩</Badge> },

];

export default class Index extends Component {
  constructor(props) {
  super(props);
  this.state = {
    // selectedTab: 'blueTab',
    hidden: false,
    fullScreen: false,
    data:[1,2],
    imgHeight:176
  };
}


  render() {
    return(
    <div className="index-container">

    <div className="tabbar-container" style={{ position: 'fixed', height: '100%', width: '100%', top: 0 }}>
        <TabBar
          unselectedTintColor="#949494"
          tintColor="#33A3F4"
          barTintColor="white"
          tabBarPosition="bottom"
        >
          <TabBar.Item
            title="首页"
            key="Life"
            icon={<div style={{
              width: '22px',
              height: '22px',
              background: 'url(./images/首页.png) center center /  21px 21px no-repeat' }}
            />
            }
            selectedIcon={<div style={{
              width: '22px',
              height: '22px',
              background: 'url(./images/首页1.png) center center /  21px 21px no-repeat' }}
            />
            }
            selected={this.state.selectedTab === 'blueTab'}

            onPress={() => {
              this.setState({
                selectedTab: 'blueTab',
              });
            }}
            data-seed="logId"
          >
          <NavBar className="method-nav"
          mode="dark"
          >住吧家居</NavBar>
              <Carousel
                autoplay={true}
                infinite={true}
                beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
                afterChange={index => console.log('slide to', index)}
              >
                {this.state.data.map(val => (
                  <a
                    key={val}
                    href=""
                    style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
                  >
                    <img
                      src={'./images/banner-'+val+'.png'}
                      alt=""
                      style={{ width: '100%', verticalAlign: 'top' }}
                      onLoad={() => {
                        // fire window resize event to change height
                        window.dispatchEvent(new Event('resize'));
                        this.setState({ imgHeight: 'auto' });
                      }}
                    />
                  </a>
                ))}
              </Carousel>
              <div className="under-container">
                <img src='./images/under_02.jpg'/>
                <img src="./images/under_01.jpg"/>
                <img src="./images/under_03.jpg"/>
              </div>
              <div className="hot-advice">
                <div className="hot-advice-title">
                  <h2>热门推荐</h2>
                </div>
                <div className="hot-advice-img-container">
                  <span>什么是英伦装修风格 英伦风家装 英伦风格装修效果图</span>
                  <img src="./images/hot.png"></img>
                </div>
              </div>
          </TabBar.Item>
          <TabBar.Item
            icon={
              <div style={{
                width: '22px',
                height: '22px',
                background: 'url(./images/收藏.png) center center /  21px 21px no-repeat' }}
              />
            }
            selectedIcon={
              <div style={{
                width: '22px',
                height: '22px',
                background: 'url(./images/收藏1.png) center center /  21px 21px no-repeat' }}
              />
            }
            title="灵感"
            key="method"
            selected={this.state.selectedTab === 'redTab'}
            onPress={() => {
              this.setState({
                selectedTab: 'redTab',
              });
            }}
            data-seed="logId"
          >

            <div>
                <NavBar className="method-nav"
                mode="light"
                rightContent={[
                  <Icon key="0" type="search" style={{ marginRight: '16px' }} />
                ]}
              ><span>灵感</span></NavBar>
            </div>

            <div>
            <Tabs tabs={tabs}
              initialPage={1}
              onChange={(tab, index) => { console.log('onChange', index, tab); }}
              onTabClick={(tab, index) => { console.log('onTabClick', index, tab); }}
            >
              <div className="item-container" style={{ height: '700px', backgroundColor: '#eeeeee' }}>
                  {
                    [1,2,3,4,5,6].map((value)=> {
                      return(
                        <div className="show-item">
                          <img src="./images/show1.jpg"/>
                          <div className="item-content">
                            <img src="./images/head.png"/>
                            <span>儿童房</span>
                            <img id="img2" className="img2" src="./images/心.png"/>
                          </div>
                        </div>
                      )
                    })
                  }
              </div>
              <div className="item-container" style={{  height: '700px', backgroundColor: '#eeeeee' }}>
              {
                [1,2,3,4,5,6].map((value)=> {
                  return(
                    <div className="show-item">
                      <img src="./images/show1.jpg"/>
                      <div className="item-content">
                        <img src="./images/head.png"/>
                        <span>儿童房</span>
                        <img id="img2" className="img2" src="./images/心.png"/>
                      </div>
                    </div>
                  )
                })
              }
              </div>
              <div style={{ height: '700px', backgroundColor: '#eeeeee' }}>
              {
                [1,2,3,4,5,6].map((value)=> {
                  return(
                    <div className="show-item">
                      <img src="./images/show1.jpg"/>
                      <div className="item-content">
                        <img src="./images/head.png"/>
                        <span>儿童房</span>
                        <img id="img2" className="img2" src="./images/心.png"/>
                      </div>
                    </div>
                  )
                })
              }
              </div>
              <div style={{ height: '700px', backgroundColor: '#eeeeee' }}>
              {
                [1,2,3,4,5,6].map((value)=> {
                  return(
                    <div className="show-item">
                      <img src="./images/show1.jpg"/>
                      <div className="item-content">
                        <img src="./images/head.png"/>
                        <span>儿童房</span>
                        <img id="img2" className="img2" src="./images/心.png"/>
                      </div>
                    </div>
                  )
                })
              }
              </div>
              <div style={{ height: '700px', backgroundColor: '#eeeeee' }}>
              {
                [1,2,3,4,5,6].map((value)=> {
                  return(
                    <div className="show-item">
                      <img src="./images/show1.jpg"/>
                      <div className="item-content">
                        <img src="./images/head.png"/>
                        <span>儿童房</span>
                        <img id="img2" className="img2" src="./images/心.png"/>
                      </div>
                    </div>
                  )
                })
              }
              </div>
              <div style={{ height: '700px', backgroundColor: '#eeeeee' }}>
              {
                [1,2,3,4,5,6].map((value)=> {
                  return(
                    <div className="show-item">
                      <img src="./images/show1.jpg"/>
                      <div className="item-content">
                        <img src="./images/head.png"/>
                        <span>儿童房</span>
                        <img id="img2" className="img2" src="./images/心.png"/>
                      </div>
                    </div>
                  )
                })
              }
              </div>
            </Tabs>
            </div>
          </TabBar.Item>
          <TabBar.Item
            icon={
              <div style={{
                width: '22px',
                height: '22px',
                background: 'url(./images/商城.png) center center /  21px 21px no-repeat' }}
              />
            }
            selectedIcon={
              <div style={{
                width: '22px',
                height: '22px',
                background: 'url(./images/商城1.png) center center /  21px 21px no-repeat' }}
              />
            }
            title="商城"
            key="shop"
            selected={this.state.selectedTab === 'shopTab'}
            onPress={() => {
              this.setState({
                selectedTab: 'shopTab',
              });
            }}
          >

            <Shop/>

          </TabBar.Item>
          <TabBar.Item
            icon={{ uri: './images/我的.png' }}
            selectedIcon={{ uri: './images/我的1.png' }}
            title="我的"
            key="my"
            selected={this.state.selectedTab === 'yellowTab'}
            onPress={() => {
              this.setState({
                selectedTab: 'yellowTab',
              });
            }}
          >
          </TabBar.Item>
        </TabBar>
      </div>
  </div>
    )
  }
}
